import PropTypes from 'prop-types';
import React, { useCallback, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { FaRegFrown, FaRegSmileBeam } from 'react-icons/fa';

import { __t } from '@mssgme/shared';
import { LandingsPropTypes } from '@mssgme/ui';
import { useCoreContext, useCRMData, useModalThemes, useViewOrder } from '../hooks';
import { SmartButton, ThemedButton, ThemedModal } from '../UI';
// import { OrderItemsList } from './OrderItem';

import styles from './PaymentStatusPage.module.scss';

PaymentStatusPage.propTypes = {
    orderId: PropTypes.string.isRequired,
    status: LandingsPropTypes.orderStatus.isRequired,
};

export function PaymentStatusPage({ orderId, status }) {
    const history = useHistory();
    const { router } = useCoreContext();
    const { form } = useCRMData();
    const { contentStyle } = useModalThemes();
    const { order, error } = useViewOrder(orderId);
    const { action: { type, url, page } } = form.successURL;
    const hasSuccessUrl = !!(type === 'page' ? page : url);
    const resolvedStatus = status === 'pending' ? 'paid' : status;

    useEffect(() => {
        if (order) {
            const { total: { amount, currency }, items } = order;
            const createOrder = new CustomEvent('createOrder', {
                detail: {
                    orderId,
                    value: amount,
                    currency,
                    items: items.map(({ name, _id, price: { amount }, quantity }) =>
                        ({
                            name,
                            id: _id,
                            price: amount,
                            quantity,
                        })
                    ),
                },
            });

            document.dispatchEvent(createOrder);
        }
    }, [order, orderId]);

    const handleClose = useCallback(
        (event) => {
            if (event?.orderId) {
                return history.push(router.orders.toView(event.orderId));
            }

            return history.push(router.toHome());
        },
        [router]
    );

    if (error) {
        return (
            <ThemedModal overlay compact showHeader={false} onClose={handleClose}>
                <div className={styles.root}>
                    <p>{__t('order_not_found')}</p>
                </div>
            </ThemedModal>
        );
    }

    return (
        <ThemedModal overlay compact showHeader={false} onClose={handleClose}>
            {(onClose) => (
                <div className={styles.root}>
                    {resolvedStatus === 'failed' ? (
                        <FaRegFrown size={60} className={styles.icon} />
                    ) : (
                        <FaRegSmileBeam size={60} className={styles.icon} />
                    )}

                    <h1 className={styles.title}>{__t(`payment_${resolvedStatus}`)}</h1>

                    {resolvedStatus !== 'failed' && (
                        <>
                            <p className={styles.subTitle}>{form.successMessage || __t('payment_paid_subtitle')}</p>
                            {hasSuccessUrl && (
                                <div className={styles.subTitle}>
                                    <SmartButton
                                        link={form.successURL}
                                        id={form.successURL.action._id}
                                        style={{
                                            background: contentStyle.buttonBackground,
                                            color: contentStyle.buttonTextColor,
                                            borderRadius: 10,
                                            minHeight: 44,
                                            marginBottom: 40,
                                        }}
                                        interactive
                                    />
                                </div>
                            )}
                        </>
                    )}

                    {/*<h2 className={styles.orderTitle}>{__t('order_adjusting')}</h2>*/}
                    {/*{order && <OrderItemsList order={order} />}*/}

                    <ThemedButton
                        fullWidth
                        style={{ marginTop: 16 }}
                        theme={contentStyle}
                        onClick={() => onClose({ orderId })}
                    >
                        {__t('view_order')}
                    </ThemedButton>
                    <ThemedButton
                        fullWidth
                        style={{ marginTop: 8 }}
                        contour
                        theme={contentStyle}
                        onClick={onClose}
                    >
                        {__t('back_to_home')}
                    </ThemedButton>
                </div>
            )}
        </ThemedModal>
    );
}
