import React from 'react';
import PropTypes from 'prop-types';

import { cls } from '@mssgme/helpers';
import { useRenderer } from '../../hooks';
import BuyButton from './BuyButton';

import styles from './ServiceContent.module.scss';

export const ServiceActionsPane = ({
    contacts,
    service,
    interactive,
    enabledPayments,
    onCartAdd,
    currency,
    ...rest
}) => {
    const renderBlock = useRenderer();
    const hasContacts = !!contacts?.length;

    return (
        <div {...cls(styles.contacts, rest)}>
            {enabledPayments && (
                <div className={styles.actionsBuy}>
                    <BuyButton
                        className={styles.actionsBuyButton}
                        service={service}
                        productCurrency={currency}
                        interactive={interactive}
                        onCartAdd={onCartAdd}
                    />
                </div>
            )}

            {hasContacts && (
                <div className={styles.actionsWrap}>{contacts?.map(renderBlock)}</div>
            )}
        </div>
    );
};

ServiceActionsPane.propTypes = {
    contacts: PropTypes.arrayOf(PropTypes.shape({ kind: PropTypes.string.isRequired })),
    service: PropTypes.object.isRequired,
    interactive: PropTypes.bool,
    enabledPayments: PropTypes.bool,
    onCartAdd: PropTypes.func.isRequired,
    currency: PropTypes.string,
};
