import PropTypes from 'prop-types';
import React from 'react';
import { useDeepCompareEffect } from 'react-use';

import { cls } from '@mssgme/helpers';
import { ScrollableBar } from '../../../../../UI';
import { ButtonsMenuItems } from './ButtonsMenuItems';

import styles from './ButtonsMenu.module.scss';

export const ButtonsMenu = ({ items, interactive, activeCollectionId, theme, onSelect, ...rest }) => {
    useDeepCompareEffect(() => {
        if (items.length) {
            onSelect(items[0]._id);
        }
    }, [items]);

    return (
        <ScrollableBar containerClass={styles.list} color={theme.color} items={items} {...cls(styles.root, rest)}>
            {(scroll) => (
                <ButtonsMenuItems
                    theme={theme}
                    activeCollectionId={activeCollectionId}
                    interactive={interactive}
                    items={items}
                    onSelect={onSelect}
                    {...scroll}
                />
            )}
        </ScrollableBar>
    );
};

ButtonsMenu.propTypes = {
    items: PropTypes.array.isRequired,
    interactive: PropTypes.bool,
    activeCollectionId: PropTypes.string,
    theme: PropTypes.object.isRequired,
    onSelect: PropTypes.func,
};
