import { useLayoutEffect } from 'react';
import { mapValues } from 'lodash';
import { __t } from '@mssgme/shared';

// todo: fix dynamic form translates and then remove this
export const useDynamicFormErrors = (errors, onChange) => useLayoutEffect(
    () => {
        onChange(mapValues(errors, (value) => __t(value)));
    },
    [errors, onChange]
);
