import { useMemo } from 'react';
import { memoize } from 'lodash';

import { mapDynamicFieldToName } from '@mssgme/shared';
import { getDefaultCountriesOptions } from '@mssgme/ui';

const getFieldDefault = memoize(
    (type, lang) => {
        if (type === 'checkbox') return [];

        if (type === 'country' && lang) {
            const options = getDefaultCountriesOptions(lang);

            for (const option of options) {
                if (option.alpha_2 === lang) {
                    return option.value;
                }
            }
        }

        return '';
    },
    (type, lang) => `${type}-${lang}`
);

export const useDynamicFormDefaults = (fields, lang) => useMemo(
    () => Object.fromEntries(
        fields.map((field, index) => [
            mapDynamicFieldToName(field, index),
            getFieldDefault(field.type, lang),
        ])
    ),
    [fields, lang]
);
