import PropTypes from 'prop-types';
import React, { useEffect } from 'react';
import { Redirect } from 'react-router-dom';
import { MdContactPhone } from 'react-icons/md';

import { __t } from '@mssgme/shared';
import { CreditCardIcon, MapPinIcon, ShoppingBagIcon } from '@mssgme/icons';
import { LandingsPropTypes } from '@mssgme/ui';
import { useCoreContext, useCRMData } from '../../../hooks';
import { Step, StepSwitch } from '../../../UI';
import { AdjustingStep, ContactInfoStep, PaymentStep, ShippingAddressStep } from './Steps';

ShoppingCartPage.propTypes = {
    order: LandingsPropTypes.order.isRequired,
    onClose: PropTypes.func.isRequired,
};

export function ShoppingCartPage({ order, onClose }) {
    const { router } = useCoreContext();
    const { form } = useCRMData();

    useEffect(() => {
        if (!order.finalized) {
            const { total: { amount, currency }, items } = order;
            const openCart = new CustomEvent('openCart', {
                detail: {
                    currency,
                    value: amount,
                    items: items.map(({ name, _id, price: { amount }, quantity }) =>
                        ({
                            name,
                            id: _id,
                            price: amount,
                            quantity,
                        })
                    ),
                },
            });

            document.dispatchEvent(openCart);
        }
    }, []);

    if (order.finalized) {
        return <Redirect to={router.orders.toStatus(order._id, order.status)} />;
    }

    return (
        <StepSwitch>
            <Step name={__t('order_adjusting')} icon={ShoppingBagIcon}>
                <AdjustingStep order={order} onClose={onClose} />
            </Step>
            <Step name={__t('order_contact_info')} icon={MdContactPhone}>
                <ContactInfoStep order={order} />
            </Step>
            {!!form.collectAddress && (
                <Step name={__t('order_shipping_address')} icon={MapPinIcon}>
                    <ShippingAddressStep order={order} />
                </Step>
            )}
            <Step name={__t('order_payment')} icon={CreditCardIcon}>
                <PaymentStep order={order} />
            </Step>
        </StepSwitch>
    );
}
