import { useAsync } from 'react-use';

import { NotFoundError } from '@mssgme/api-client';
import { isMatchedById } from '@mssgme/shared';
import { useFrontAPI, useUserOrders } from '../hooks';

export function useOrder(orderId) {
    const api = useFrontAPI();
    const userOrders = useUserOrders() || [];
    const { loading, error, value: immediate } = useAsync(
        () => api.orders.show({ orderId, query: { complete: true } }),
        [orderId]
    );
    const isNotFound = error && NotFoundError.is(error);
    const order = userOrders.find(isMatchedById(orderId)) || immediate;

    return {
        isNotFound,
        loading,
        order,
        error,
        immediate,
    };
}
