import React from 'react';
import PropTypes from 'prop-types';

import { cls } from '@mssgme/helpers';
import { usePageTheme } from '../../../hooks';

import styles from './OrderViewPage.module.scss';

export const Section = ({ title, ...rest }) => {
    const { theme } = usePageTheme();

    return (
        <>
            {title && <h3 className={styles.sectionHeading}>{title}</h3>}
            <div style={theme.blocks.features} {...cls(styles.pane, rest)} />
        </>
    );
};

Section.propTypes = {
    title: PropTypes.any,
};
