import PropTypes from 'prop-types';
import React from 'react';

import { cls } from '@mssgme/helpers';
import { DynamicFormInput } from './DynamicFormInput';
import { DynamicFormTypes } from './dynamicFormTypes';

import styles from './DynamicFormInputs.module.scss';

export const DynamicFormInputs = ({ items, theme, lang, ...rest }) => {
    return (
        <ul {...cls([styles.root, styles[theme.gridMode]], rest)}>
            {items.map((item, index) => (
                <DynamicFormInput
                    as="li"
                    key={item._id || index}
                    index={index}
                    input={item}
                    theme={theme}
                    lang={lang}
                />
            ))}
        </ul>
    );
};

DynamicFormInputs.propTypes = {
    items: DynamicFormTypes.inputs.isRequired,
    theme: PropTypes.object.isRequired,
    lang: PropTypes.string,
};
