import { useMemo } from 'react';

import { createDynamicInputsValidationSchema } from '@mssgme/shared';
import { useDynamicFormDefaults } from './useDynamicFormDefaults';

export const useDynamicForm = ({ items, lang = navigator.language }) => {
    const validationSchema = useMemo(() => createDynamicInputsValidationSchema(items), [items]);
    const initialValues = useDynamicFormDefaults(items, lang);

    return {
        validationSchema,
        initialValues,
    };
};
