import { useMemo } from 'react';
import { SubscriptionFormBlockKind } from '@mssgme/shared';
import { useModalThemes } from './useModalThemes';

export const useInputTheme = (overrideTheme) => {
    const { theme } = useModalThemes(SubscriptionFormBlockKind);
    const customTheme = overrideTheme || theme;

    return useMemo(
        () => ({
            input: {
                backgroundColor: customTheme.inputBackgroundColor,
                color: customTheme.inputTextColor,
                border: 'none',
                borderRadius: customTheme.borderRadius,
            },
            error: {
                color: customTheme.inputTextColor,
                padding: '2px 8px',
                backgroundColor: customTheme.inputBackgroundColor,
                display: 'inline-block',
                borderRadius: customTheme.borderRadius,
            },
        }),
        [customTheme]
    );
};
