import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useField } from 'formik';

import { __t, DYNAMIC_FIELD_SIZE_NORMAL, mapDynamicFieldToName } from '@mssgme/shared';
import { FormField, Input, Textarea, SelectSimple, RadioGroup, CheckboxGroup, CountrySelect } from '@mssgme/ui';

import { useInputTheme } from '../../hooks';
import { DynamicFormTypes } from './dynamicFormTypes';

import styles from './DynamicFormInput.module.scss';

const specificComponents = {
    select: SelectSimple,
    radio: RadioGroup,
    checkbox: CheckboxGroup,
    country: CountrySelect,
};

const inputSpecificTypes = {
    name: 'text',
    phone: 'tel',
};

const handleEnterKey = (e) => {
    if (e.key === 'Enter') {
        e.preventDefault();
    }
};

export const isFieldPropSupported = (type, feature) => {
    switch (feature) {
        case 'paneProp':
            return ['radio', 'checkbox'].includes(type);
        case 'maxLength':
            return !['date', 'time', 'number'].includes(type);
        case 'hasPlaceholderOption':
            return type === 'select';
        case 'lang':
            return type === 'country';
        default:
            return false;
    }
};

const getFieldProps = ({ label, type, required, size, options }, lang) => {
    const specificComponent = specificComponents[type];
    const maxLength = size || DYNAMIC_FIELD_SIZE_NORMAL;
    const props = {
        required,
        label: label || type,
        component: specificComponent,
    };

    if (specificComponent) {
        const optionsArray = options?.split(/\r?\n/).filter(Boolean).map((option) => ({ label: option, value: option }));

        if (isFieldPropSupported(type, 'hasPlaceholderOption')) {
            optionsArray.unshift({ label: __t('not_selected'), value: '' });
        }

        props.options = optionsArray;

        if (isFieldPropSupported(type, 'paneProp')) {
            props.pane = true;
        }
    } else if (maxLength > DYNAMIC_FIELD_SIZE_NORMAL && type === 'text') {
        props.component = Textarea;
    } else {
        props.component = Input;
        props.type = inputSpecificTypes[type] || type;
        props.onKeyPress = handleEnterKey;

        if (type === 'number') {
            props.min = 0;
            props.inputMode = 'numeric';
            props.pattern = '[0-9]*';
        }
    }

    if (isFieldPropSupported(type, 'maxLength')) {
        props.maxLength = maxLength;
    }

    if (isFieldPropSupported(type, 'lang')) {
        props.lang = lang;
    }

    return props;
};

export const DynamicFormInput = ({ index, input, as, theme, lang }) => {
    const style = useInputTheme(theme);
    const fieldProps = useMemo(() => getFieldProps(input, lang), [input, lang]);
    const [field, meta] = useField(mapDynamicFieldToName(input, index));

    return (
        <FormField
            shakeError
            as={as}
            errors={meta.touched && meta.error}
            style={style.input}
            errorStyle={style.error}
            inputClassName={styles.input}
            labelClassName={styles.label}
            {...field}
            {...fieldProps}
        />
    );
};

DynamicFormInput.propTypes = {
    input: DynamicFormTypes.input.isRequired,
    index: PropTypes.number.isRequired,
    as: PropTypes.oneOfType([PropTypes.string, PropTypes.elementType]),
    theme: PropTypes.object,
    lang: PropTypes.string,
};
