import PropTypes from 'prop-types';
import cn from 'classnames';
import React from 'react';
import { useDeepCompareEffect } from 'react-use';

import { cls } from '@mssgme/helpers';
import { renderNode, useHorizontalTabbedScroll } from '@mssgme/ui';
import { useBasePageColor } from '../../hooks';

import styles from './ScrollableBar.module.scss';

export const ScrollableBar = ({ items, color, containerClass, children, ...rest }) => {
    const scroll = useHorizontalTabbedScroll(items.length);
    const { root, list, hasLeftBtn, hasRightBtn, moveLeft, moveRight, moveTo } = scroll;
    const { backgroundColor } = useBasePageColor();

    useDeepCompareEffect(() => {
        if (items.length) {
            moveTo(0);
        }
    }, [items]);

    containerClass = cn(styles.list, hasLeftBtn && styles.clipLeft, hasRightBtn && styles.clipRight, containerClass);

    return (
        <div ref={root} {...cls(styles.root, rest)}>
            {hasLeftBtn && (
                <div key={`le-${backgroundColor}`} className={styles.controlContainerLeft} onClick={moveLeft}>
                    <div className={styles.control} style={{ color }}>
                        &larr;
                    </div>
                </div>
            )}

            <ul ref={list} className={cn(styles.list, containerClass)}>
                {renderNode(children, scroll)}
            </ul>

            {hasRightBtn && (
                <div key={`ri-${backgroundColor}`} className={styles.controlContainerRight} onClick={moveRight}>
                    <div className={styles.control} style={{ color }}>
                        &rarr;
                    </div>
                </div>
            )}
        </div>
    );
};

ScrollableBar.propTypes = {
    items: PropTypes.array.isRequired,
    color: PropTypes.string,
    containerClass: PropTypes.string,
};
