import React from 'react';
import { Provider } from 'react-redux';

import { AnimateableRoutes, usePresenceRemoval } from '@mssgme/ui';
import { useCoreContext } from '../../../hooks';
import { CollectionView, ProductView } from '../../Product';
import { PaymentStatusPage, UserOrderPage, UserOrdersPage } from '../../Order';

const ordersRouting = {
    path: 'orders',
    component: UserOrdersPage,
    exact: true,
    children: [
        {
            path: ':orderId',
            component: UserOrderPage,
            children: [
                { path: 'payment/:status', component: PaymentStatusPage },
            ],
        },
    ],
};

const productRouting = {
    path: 'p/:productSlug',
    component: ProductView,
    children: [
        ordersRouting,
    ],
};

const routing = [
    ordersRouting,
    productRouting,
    {
        path: 'c/:collectionSlug',
        component: CollectionView,
        children: [
            productRouting,
        ],
    },
];

export function PageProductsRouting(props) {
    const { store } = useCoreContext();

    usePresenceRemoval();

    const content = <AnimateableRoutes routing={routing} {...props} />;

    return store ? <Provider store={store}>{content}</Provider> : content;
}
