import { useMemo } from 'react';

import { MessengersBlockKind } from '@mssgme/shared';
import { useCRMData } from '../hooks';

export function useContacts(map) {
    const { contacts: { messengers } } = useCRMData();

    return useMemo(
        () =>
            messengers.length ? [
                {
                    _id: `messengers-${messengers.map(({ _id }) => _id).join('-')}`,
                    kind: MessengersBlockKind,
                    messengers: map ? messengers.map(map) : messengers,
                    enabled: true,
                },
            ] : undefined,
        [messengers, map]
    );
}
